export const VIEW_REGISTRY_ACTION_TYPES = {
	// View Registry product
  VIEW_REGISTRY_PRODUCTS: "VIEW_REGISTRY_PRODUCTS",
  
  // Filtered Products
  SET_SORTED_PRODUCTS: "SET_SORTED_PRODUCTS",   
  ACTIVE_SORT_FILTER: "ACTIVE_SORT_FILTER",
  IS_GUEST_VIEW: "IS_GUEST_VIEW",
  SET_VIEW_FILTER_CATEGORY: "SET_VIEW_FILTER_CATEGORY",

  // Filter
	SET_VIEW_PARENT_FILTER: "SET_VIEW_PARENT_FILTER",
	SET_VIEW_CHILD_FILTER: "SET_VIEW_CHILD_FILTER",
	CLEAR_VIEW_FILTER: "CLEAR_VIEW_FILTER",
}

export const setViewRegistryProducts = params => {
	return {
		type: VIEW_REGISTRY_ACTION_TYPES.VIEW_REGISTRY_PRODUCTS,
		payload: params
    }
}

export const setSortedFilteredProduct = (params) => {
  return {
    type: VIEW_REGISTRY_ACTION_TYPES.SET_SORTED_PRODUCTS,
    payload: params
  }
}

export const activeSortFilter = (params) => {
  return {
    type: VIEW_REGISTRY_ACTION_TYPES.ACTIVE_SORT_FILTER,
    payload: params
  }
}

export const setguestViewRegistry = (params) => {
  return {
    type: VIEW_REGISTRY_ACTION_TYPES.IS_GUEST_VIEW,
    payload: params
  }
}

// View Filter Category Section
export const setViewFilterCategory = params => {
	return {
		type: VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_FILTER_CATEGORY,
		payload: params
	}
}


// Filter Section

export const setParentViewFilter = params => {
	return {
		type: VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_PARENT_FILTER,
		payload: params
	}
}

export const setChildViewFilter = params => {
	return {
		type: VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_CHILD_FILTER,
		payload: params
	}
}

export const clearViewFilter = () => {
	return {
		type: VIEW_REGISTRY_ACTION_TYPES.CLEAR_VIEW_FILTER,
	}
}