import React from 'react';
import { connect } from "react-redux";
import AddGift from "modules/commons/components/add-gift/AddGift";
import SortFilter from "../sort-filter/SortFilter";
import './SortFilterSection.scss';
import { isGotIt, clearViewFilter, setChildViewFilter } from "modules/view-registry/ViewRegistryActions";
import { UPDATE_REGISTRY } from "api/mutations";
import { graphQL } from "api/axios";
import { updateRegistryDetail } from "utils/commonFunction";
import FilterItemChip from "modules/my-registry/components/filter/filter-item-chip/FilterItemChip"

class SortFilterSection extends React.Component {

  handleIsGotIt = () => {
    let data = {
      query: UPDATE_REGISTRY,
      operationName: "updateRegistryDetail",
      variables: {
        "registryId": this.props.stateRegistryInfo.id,
        "input": {
          isFeaturedNoticeRead: true
        }
      }
    }
    graphQL(data).then(res => {
      try {
        if (res.data.data.updateRegistry.registry) {
          // this.props.dispatch(isGotIt(true))
          updateRegistryDetail(this.props.dispatch);
        } else {
          console.log(res.data.data.updateRegistry.errors);
          console.error("Some error occurred");
        }
      } catch (e) {
        console.error(e);
      }
    }).catch(err => {
      console.error(err.message)
    })
  }

  // Filter chips close handler
  deleteChipHandler = (isParent, childId) => {
    if (isParent) {
      this.props.dispatch(clearViewFilter())
    } else {
      const newChildArray = this.props.stateFilterApplied.childFilter.filter(element => {
        return element !== childId
      })
      this.props.dispatch(setChildViewFilter(newChildArray))
    }
  }

  //Clear filter state
  clearFilterHandler = (ev) => {
    ev.preventDefault()
    this.props.dispatch(clearViewFilter())
  }

  render() {
    let filterChips = {}
    if (this.props.stateFilterApplied) {
      let allChild = []
      for (let filterCat of this.props.stateFilterCategory) {
        for (let childCat of filterCat.child) {
          allChild.push(childCat)
        }
      }

      if (this.props.stateFilterApplied.childFilter) {
        filterChips.childFilter = this.props.stateFilterApplied.childFilter.map(item => {
          return allChild.find(element => {
            return element.id === item
          })
        })
      }
    }

    return (
      <>
        <div className="row">
          <div className="col">
            <h4 className="font-weight-bold mr-2 filter-section-total-items">{this.props.productCount} {this.props.productCount === 1 ? "Gift" : "Gifts"}</h4>
          </div>
          <div className="col-5 filter-sort-section">
            <div className="d-flex justify-content-end">
              <div className="text-sm sort-by-text mr-2 align-self-center">Sort by</div>
              <SortFilter />
              {this.props.stateGuestViewRegistry ?
                <></> :
                <div className="ml-4 filter-add-gift-btn"><AddGift /></div>}
            </div>

          </div>
        </div>
        <div className="row">
          <div className="col-7">
            <div className="filtered-items">
              {filterChips.childFilter ?
                filterChips.childFilter.map(item =>
                  <FilterItemChip
                    text={item.display}
                    className="mr-2"
                    key={filterChips.id + "-" + item.id}
                    deleteHandler={() => this.deleteChipHandler(false, item.id)} />)
                : null}
              {this.props.stateFilterApplied.childFilter.length > 0 ?
                <a className="font-medium text-sm filter-clear-link" href="#" tabindex
                  onClick={(ev) => this.clearFilterHandler(ev)}>Clear
                all</a>
                : null}
            </div>
          </div>
        </div>
        {this.props.stateRegistryInfo.isFeaturedNoticeRead === false && this.props.stateActiveSortFilter.sortId !== 1 && !this.props.stateGuestViewRegistry ?
          <div className="row mt-2 mb-3">
            <div className="col">
              <div className="text-sm py-2 d-flex justify-content-center sort-filter-warning">
                <div>Enable rearrange gifts by</div>
                <div className="font-weight-bold px-1">clearing your filters</div>
                <div>and setting to</div>
                <div className="font-weight-bold px-1">Sort by: Featured.</div>
                <div className="sort-filter-warning-btn" onClick={this.handleIsGotIt}>Ok, got it.</div>
              </div>
            </div>
          </div> : <></>
        }
      </>
    )
  }
}


const mapStateToProps = state => {
  return {
    stateViewRegistryProducts: state.viewRegistry.viewRegistryProducts,
    stateActiveSortFilter: state.viewRegistry.activeSortFilter,
    stateGuestViewRegistry: state.viewRegistry.guestViewRegistry,
    stateRegistryInfo: state.registry.info,
    stateFilterApplied: state.viewRegistry.viewFilterApplied,
    stateFilterCategory: state.viewRegistry.filterCategory
  }
}

export default connect(
  mapStateToProps
)(SortFilterSection);
