const GET_USERS = gql`
query getUsers {
    users {
        id,
        email
    }
}
`;

const GET_CATEGORIES = `
query getCategories($registryId: Int!){
  registryCategories(registryId:$registryId){
    id,
    isExcluded,
    itemsCount,
    
    addedItemsCount,
    totalItemsCount,
    category{
      id,
      categoryName,
      isParent,
    },
    registrySubCategories{
      id,
      isExcluded,
      itemsCount,
      addedItemsCount,
      totalItemsCount,
      category{
        id,
        categoryName,
        isParent,
      },
      registrySubProductTypes{
        id,
        isExcluded,
        itemsCount,
        addedItemsCount,
        totalItemsCount,
        category{
          id,
          categoryName,
          isParent,
        },
      }
    }
  }
 }
`;

const GET_PRODUCTS = `
query registryItems($registryId:Int!){
    registryItems(registryId:$registryId){
      edges{
        node{
          id,
          registryitemImage{
            image
          }
          isGroupGift,
          isReserved,
          quantity,
          mostWanted,
          featuredOrder,
          orderNo,
          giftMessage,
          isReserved,
          isAmountReceived,
          isPurchased,
          title,
          registryCategories {
            id,
            category{
              categoryName,
            }
          }
          note,
          item{
            store{
              id,
              storeName
            }
            id,
            shopifyItemId,
            sku,
            price,
            imageUrl,
          }
        }
      }
    }
  }

`

const GET_RECOMMENDATIONS = `
query recommendation($productCount: Int!, $productType: String!){
  products(first:$productCount,
    query:$productType){
      edges{
        node{
          id,
          handle,
          title,
          description,
          vendor,
          onlineStoreUrl,
          variants (first:1){
            edges {
              node {
                price,
                sku,
                image{
                  originalSrc
                }
              }
            }
          }
        }
      }
    }
  }
`;

const ADD_REGISTRY_ITEM = `
mutation addRegistryItems(
    $registryId:Int!,
    $title:String!,
    $category:[Int]!,
    $tags:[Int]!,
    $storeItemUrl:String!,
    $sku:String!,
    $price:Float!,
    $quantity:Int!,
    $shopifyItemId:String,
    $imageUrl: String){
  addRegistryItems(registryId:$registryId,
  registryItems:[{
    storeName:"monicaandandy",
    items: [{
      title:$title,
      registryCategories:$category,
      tags:$tags,
      storeItemUrl:$storeItemUrl,
      sku:$sku,
      price:$price,
      quantity:$quantity,
      shopifyItemId: $shopifyItemId,
      imageUrl: $imageUrl
    },]
  }])
  {
    message
  }
}
`;

const UPDATE_REGISTRY_ITEM = `
mutation updateRegistryItems($registryItemId: Int!, $quantity: Int!){
  updateRegistryItems(registryItemId:$registryItemId, registryItemData:{
    quantity:$quantity,
    isReserved:false,
  }){
    message
  }
}
`;

const REMOVE_REGISTRY_ITEM = `
mutation deleteRegistryItems($registryItemId: Int!){
  deleteRegistryItems(registryItemId:$registryItemId){
    message
  }
}
`;

