import React from "react";
import { ToastContainer, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import './Toast.scss';

const Toast = () => {
  const closeToastMessage = (parentId) => {
    toast.dismiss(parentId);
  }

  return (
    <ToastContainer
      position="top-center"
      className="m-a-toast-container"
      toastClassName="m-a-toast"
      autoClose={4000}
      hideProgressBar={true}
      newestOnTop={true}
      draggable={false}
      closeOnClick={false}
      closeButton={<i className="icon-close m-a-toast-close" onClick={(event) => closeToastMessage(event.target.parentElement.id)} />}
    />
  )
}

export default Toast;