import React from "react";
import { GET_CATEGORIES } from "../../../../api/queries";
import { graphQL } from "../../../../api/axios";
import { setPersonaliseCheckListItems } from "../../PersonaliseCheckListActions";
import { connect } from "react-redux";
import './EditChecklist.scss';
import Counter from "../../../commons/components/counter/Counter";
import {
  incrementCheckListQuantity,
  setIsActiveCheckList,
  decrementCheckListQuantity
} from "../../PersonaliseCheckListActions";
import Button from "../../../commons/components/button-component/ButtonComponent";
import routes from "utils/routes";
import { SAVE_UPDATED_CHECKLIST } from "api/mutations";
import { toast } from "react-toastify";
import Toast from "modules/commons/components/toast/Toast";
import { getAllCategories } from 'utils/commonFunction';

// import {setCheckListItems} from "../../../my-registry/MyRegistryActions";

class PersonaliseChecklist extends React.Component {
  componentDidMount() {
    this.getAllCategories(this.props.stateRegistryInfo.id, this.props.dispatch);
  }

  getAllCategories = (registryId, dispatch) => {
    let data = {
      query: GET_CATEGORIES,
      variables: {
        registryId: parseInt(registryId)
      },
    }
    graphQL(data).then(res => {
      if (res.data.errors || res.data.error) {
        // TODO: Add toast
        console.log('Something went wrong-->', res.data.errors)
        return;
      } else {
        let resData = getAllCategories(res.data.data.registryCategories, true)
        let finalCheckList = []
        // For Parent categories
        for (let category of resData) {
          let finalSubCategory = []

          if (category.subCategories.length) {
            // For Sub-Categories
            for (let subCategory of category.subCategories) {
              let tempSubCategory = []
              if (subCategory.subProductTypes.length) {

                // For subProductType
                for (let subProductType of subCategory.subProductTypes) {
                  subProductType.categoryName = subCategory.categoryName + ' (' + subProductType.categoryName + ')'
                  if (subProductType.isExcluded) {
                    subProductType.totalItemsCount = 1
                    subProductType['isAdded'] = false
                  } else {
                    subProductType['isAdded'] = true
                  }
                  tempSubCategory.push(subProductType)
                }
              }
              else {
                if (subCategory.isExcluded) {
                  subCategory.totalItemsCount = 1
                  subCategory['isAdded'] = false
                } else {
                  subCategory['isAdded'] = true
                }
                tempSubCategory.push(subCategory)
              }
              finalSubCategory.push(...tempSubCategory)
            }
            let tempCategory = { ...category }
            tempCategory.subCategories = finalSubCategory
            finalCheckList.push(tempCategory)
          }

        }
        dispatch(setPersonaliseCheckListItems(finalCheckList))
      }
    }).catch(err => {
      console.error('Something went wrong-->', err);
    })
  }

  closeAndSavePersonaliseCheckList = (event) => {
    event.preventDefault();
    let finalCheckListData = []
    let tempStateCheckList = [...this.props.stateCheckList]
    for (let category of tempStateCheckList) {
      for (let subCategory of category.subCategories) {
        // let tempData = null
        let tempData = {
          registryCategoryId: subCategory.id,
          itemsCount: subCategory.totalItemsCount,
          isExcluded: !subCategory.isAdded
        }
        finalCheckListData.push(tempData)
      }
    }

    let registryId = this.props.stateRegistryInfo.id
    let data = {
      query: SAVE_UPDATED_CHECKLIST,
      variables: {
        registryId: registryId,
        itemCountData: finalCheckListData
      },
    }

    graphQL(data).then(res => {
      if (res.data) {
        toast.success((
          <p>Personalise Checklist Updated Successfully</p>
        ))
        this.props.history.push(routes.REGISTRY.HOME)
      }

    }).catch(err => {
      console.error(err.message)
      toast.error((
        <p>Some error occurred.</p>
      ))
    })
  }

  gotoPersonaliseQuestions = (event) => {
    event.preventDefault();
    this.props.history.push(routes.PERSONALIZE.HOME)
  }

  render() {
    return (
      // TODO: API call
      <div className="personalise-checklist">
        {/* Header */}
        <div className="py-2 personalise-checklist-header font-body font-medium">
          <div className="container">
            <div className="row align-items-center">
              <div className="col ">Edit Checklist</div>
              <div className="col d-flex align-items-center justify-content-end">
                <a className="mr-6 personalise-header-link" tabindex onClick={(e) => { this.gotoPersonaliseQuestions(e) }}>Take our quiz</a>
                <div>
                  <Button
                    variant="primary"
                    className="px-3 py-1  font-medium done-btn"
                    onClick={(e) => { this.closeAndSavePersonaliseCheckList(e) }}
                  >Im Done</Button>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* All categries selection body */}
        {this.props.stateCheckList.map((parentCategories, i) => (
          <div className="mt-6 container" key={parentCategories.categoryName + "-" + i}>
            <div className="checklist-title pb-4 body-text">
              {parentCategories.categoryName}
            </div>
            <div className="row mt-4">
              {parentCategories.subCategories.map((subCategories, i) => (
                <div className="col-3 mb-5" key={subCategories.id}>
                  <div className={["checklist-card pl-2 pr-3 py-3",
                    !subCategories.isAdded ? "add-checklist-bg" : ""].join(' ')
                  }>
                    <div className="d-flex mb-4 checklist-action-btn">
                      <div className="text-body font-medium align-self-center">{subCategories.categoryName}</div>
                      {subCategories.addedItemsCount <= 0 ?
                        <div className="ml-auto">
                          <button className={["checklist-add-btn",
                            !subCategories.isAdded ? "checklist-icon-plus" : "icon-close"].join(' ')}
                            onClick={() => this.props.dispatch(setIsActiveCheckList(subCategories.id, !subCategories.isAdded))} />
                        </div>
                        : null
                      }
                    </div>
                    <div className="d-flex align-items-center">
                      <Counter
                        counter={subCategories.totalItemsCount}
                        incrementHandler={() => this.props.dispatch(incrementCheckListQuantity(subCategories.id, 1))}
                        decrementHandler={() => this.props.dispatch(decrementCheckListQuantity(subCategories.id, 1))}
                      />
                      {subCategories.addedItemsCount > 0 ?
                        <span className="ml-auto text-sm item-count-label">{subCategories.addedItemsCount} added</span>
                        : null
                      }
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ))}
        <Toast />
      </div>
    );
  }

};

const mapStateToProps = state => {
  return {
    stateCheckList: state.personaliseCheckList.editedPersonaliseCheckList,
    stateRegistryInfo: state.registry.info
  }
}


export default connect(
  mapStateToProps
)(PersonaliseChecklist);