import React from "react";
import {
  GET_PRODUCTS,
  REMOVE_REGISTRY_ITEM,
  GET_CATEGORIES,
  GET_REGISTRIES,
  ADD_REGISTRY_ITEM,
  UPDATE_REGISTRY_ITEM,
  GET_QUESTIONS,
  GET_GUEST_REGISTRY,
  GET_PERSONALIZE_QUESTIONS
} from "../api/queries";
import {graphQL, graphQLNoToken} from "../api/axios";
import {MEDIA_URL_PREFIX} from "./constants";
import {
  setRegistryProducts,
  setCheckListItems,
  toggleMustHaveProduct as toggleMustHaveProductAction
} from "../modules/my-registry/MyRegistryActions";
import {CREATE_REGISTRY, TOGGLE_MUST_HAVE, UPDATE_VIEW_REGISTRY_POISTION} from "../api/mutations";
import {setOnboardingRegistry, updateRegistryInfo} from "../modules/registry/RegistryActions";
import {setViewRegistryProducts} from "../modules/view-registry/ViewRegistryActions";
import {addOnboardingQuestion} from "../modules/onboarding/OnboardingAction";
import {addPersonalizeQuestion} from "../modules/personalise-checklist/PersonaliseCheckListActions";
import {updateGuestViewInfo} from "../modules/guest-view/GuestViewAction";
import {toast} from "react-toastify";

export const getProducts = (registryId, dispatch) => {
  let data = {
    query: GET_PRODUCTS,
    variables: {
      registryId: parseInt(registryId)
    }
  }
  graphQL(data).then(res => {
    let responseData = res.data.data.registryItems.edges.map(item => {
      // return { ...item.node }
      let newItemObj = {...item.node}
      delete newItemObj.registryitemImage;
      if (item.node.registryitemImage.length > 0) {
        newItemObj.item.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + item.node.registryitemImage[0].image;
      }
      return newItemObj;
    })
    // TODO: change category
    let finalData = responseData.filter(product => product.registryCategories.length > 0)
    dispatch(setRegistryProducts(finalData))

  }).catch(err => {
    console.error(err.response)
  })
}

export const toggleMustHaveProduct = (product, dispatch) => {
  let data = {
    query: TOGGLE_MUST_HAVE,
    variables: {
      productId: product.id,
      mustHaveValue: !product.mostWanted,
      quantity: product.quantity
    }
  }
  graphQL(data).then(res => {
    if (res.status === 200) {
      dispatch(toggleMustHaveProductAction(product.item.id))
    }
  }).catch(err => {
    console.error(err.message)
  })
}

export const removeProductFromCartHandler = (card, registryId, dispatch,) => {
  let registryItemId = card.id
  let data = {
    query: REMOVE_REGISTRY_ITEM,
    variables: {
      registryItemId: registryItemId
    },
  }
  graphQL(data).then(res => {
    if (res.data.error) {
      return;
    } else {
      // this.props.dispatch(removeProductFromCart(card.item.id))
      // this.getProducts();
      getProducts(registryId, dispatch);
      getCategories(registryId, dispatch);

      toast.error((
        <p><b>{card.title}</b> removed from your registry!</p>
      ))

      // getCategories()
    }

  }).catch(err => {
    console.error(err.message)
    toast.error((
      <p>Some error occurred.</p>
    ))
  })
}

export const getRefinedCategory = (category) => {
  return ({
    id: category.id,
    categoryName: category.category.categoryName,
    isDesired: category.isDesired,
    isExcluded: category.isExcluded,
    isDefaultVisible: category.isDefaultVisible,
    addedItemsCount: category.addedItemsCount,
    totalItemsCount: category.totalItemsCount,
    isParent: category.category.isParent,
  })
}


// Get all categories
export const getAllCategories = (registryCategories, getAll) => {
  let parentCategories = registryCategories.filter(category => getAll ? true : !category.isExcluded)
  let finalCategories = parentCategories.map(parentCategory => {
    let newParentCategory = getRefinedCategory(parentCategory)
    let subCategory = parentCategory.registrySubCategories;
    delete parentCategory.registrySubCategories
    let newSubCategories = subCategory.filter(cat => getAll ? true : !cat.isExcluded)
    newSubCategories = newSubCategories.map(cat => {
      let refinedSubCate = getRefinedCategory(cat)
      let subProductTypes = cat.registrySubProductTypes.map(subProduct => getRefinedCategory(subProduct))
      refinedSubCate['subProductTypes'] = subProductTypes.filter(category => getAll ? true : !category.isExcluded)
      return refinedSubCate
    })
    newParentCategory['subCategories'] = newSubCategories
    return newParentCategory
  })

  return finalCategories
}

export const getCategories = (registryId, dispatch) => {
  let data = {
    operationName: "getCategories",
    query: GET_CATEGORIES,
    variables: {
      registryId: parseInt(registryId)
    },
  }
  graphQL(data).then(res => {
    if (res.data.errors || res.data.error) {
      // TODO: Add toast
      console.log('Something went wrong-->', res.data.errors)
      return;
    } else {
      let finalCategories = getAllCategories(res.data.data.registryCategories, false)
      if (finalCategories.length === 0) {
        let allCategories = getAllCategories(res.data.data.registryCategories, true)
        dispatch(setCheckListItems(allCategories))
      } else {
        dispatch(setCheckListItems(finalCategories))
      }
    }
  })
}

export const updateRegistryDetailState = (dispatch, registryData) => {
  let currentRegistryInfo = null;

  currentRegistryInfo = registryData[0].node;
  if (currentRegistryInfo.image) {
    currentRegistryInfo.image.image = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + currentRegistryInfo.image.image;
  }

  if (currentRegistryInfo) {
    dispatch(updateRegistryInfo({...currentRegistryInfo}))
  } else {
    dispatch(updateRegistryInfo({notFound: true}))
    console.log("No registry found!");
  }
}

export const updateRegistryDetail = (dispatch) => {
  let data = {
    query: GET_REGISTRIES
  }

  graphQL(data).then(res => {
    try {
      if (res.data.data.registries.edges) {
        updateRegistryDetailState(dispatch, res.data.data.registries.edges)
      } else {
        dispatch(updateRegistryInfo({notFound: true}))
      }
    } catch (e) {
      dispatch(updateRegistryInfo({notFound: true}))
      console.error(e)
    }
  }).catch(err => {
    dispatch(updateRegistryInfo({notFound: true}))
    console.error(err.response ? err.response.data : null)
  })
}

export const updateViewRegistryPosition = (registryId, position, oldPosition, dispatch) => {
  let data = {
    query: UPDATE_VIEW_REGISTRY_POISTION,
    variables: {
      registryId: registryId,
      registryItems: position
    },
  }
  graphQL(data).then(res => {
    if (res.data.error) {
      // TODO: Add toast
      dispatch(setViewRegistryProducts(oldPosition))
      toast.error("Somerhing went wrong while re-arranging Products")
      console.log('Something went wrong-->', res.data.error)
      return;
    }

  }).catch(err => {
    dispatch(setViewRegistryProducts(oldPosition))
    console.error(err.message)
  })
}

export const addProductHandler = (card, stateAddedProducts, stateRegistryInfo, dispatch) => {
  let existingProduct = stateAddedProducts.filter(product => product.item.sku === card.variants.edges[0].node.sku)
  const registryId = parseInt(stateRegistryInfo.id)
  if (existingProduct.length === 0) {
    const title = card.title
    const category = card.category.id
    const storeItemUrl = card.onlineStoreUrl
    const sku = card.variants.edges[0].node.sku
    const price = parseFloat(card.variants.edges[0].node.price)
    const quantity = parseInt(card.quantity)
    const shopifyItemId = card.id
    const imageUrl = card.variants.edges[0].node.image.originalSrc

    let data = {
      query: ADD_REGISTRY_ITEM,
      variables: {
        registryId: registryId,
        title: title,
        category: category,
        tags: [],
        storeItemUrl: storeItemUrl ? storeItemUrl : '',
        sku: sku,
        price: price,
        quantity: quantity,
        shopifyItemId: shopifyItemId,
        imageUrl: imageUrl,
      }
    }
    graphQL(data).then(_ => {
      getProducts(registryId, dispatch)
      getCategories(registryId, dispatch)

      toast.success((
        <p><b>{card.title}</b> added to your registry!</p>
      ))
    }).catch(err => {
      console.error(err.message)

      toast.error((
        <p>Some error occurred!</p>
      ))
    })
  } else {
    let data = {
      query: UPDATE_REGISTRY_ITEM,
      variables: {
        registryItemId: existingProduct[0].id,
        quantity: parseInt(card.quantity) + parseInt(existingProduct[0].quantity),
      }
    }
    graphQL(data).then(res => {
      getProducts(registryId, dispatch)
      getCategories(registryId, dispatch)

      toast.success((
        <p><b>{card.title}</b> added to your registry!</p>
      ))
    }).catch(err => {
      console.error(err.message)

      toast.error((
        <p>Some error occurred!</p>
      ))
    })
  }
}

export const getOnboardingQuestions = (dispatch) => {
  let reqData = {
    query: GET_QUESTIONS,
    variables: {
      isPersonalize: false
    }
  }

  graphQLNoToken(reqData).then(res => {
    if (res.data.data.questions) {
      const questions = res.data.data.questions.map(question => {
        question.options = question.options.map(option => {
          if (option.followUpQuestions.length > 0) {
            option.followUpQuestions.map(followUpQuestion => {
              followUpQuestion['parentQuestion'] = question.id
              return followUpQuestion;
            })
          }
          return option;
        })
        question.isAnswered = false;
        return question;
      })
      dispatch(addOnboardingQuestion(questions))
    }
  }).catch(err => {
    // toast.error("Error occurred while fetching questions");
    console.error(err);
  })
}


export const getPersonalizeQuestions = (registryId, dispatch) => {
  let reqData = {
    query: GET_PERSONALIZE_QUESTIONS,
    variables: {
      registryId: registryId,
    }
  }

  graphQL(reqData).then(res => {
    if (res.data.data.editablePersonalizeQuestions) {
      const questions = res.data.data.editablePersonalizeQuestions.map(question => {
        question.options = question.options.map(option => {
          if (option.followUpQuestions.length > 0) {
            option.followUpQuestions.map(followUpQuestion => {
              followUpQuestion['parentQuestion'] = question.id
              return followUpQuestion;
            })
          }
          return option;
        })
        question.isAnswered = false;
        return question;
      })
      dispatch(addPersonalizeQuestion(questions))
    }
  }).catch(err => {
    toast.error("Error occurred while fetching questions");
    console.error(err);
  })
}

export const updateGuestViewRegistry = (urlSlug, dispatch) => {
  let reqData = {
    query: GET_GUEST_REGISTRY,
    variables: {
      urlSlug: urlSlug
    }
  }

  graphQL(reqData).then(res => {
    if (res.data.data.registryAsGuest) {
      dispatch(updateGuestViewInfo(res.data.data.registryAsGuest))
    }
  }).catch(err => {
    dispatch(updateGuestViewInfo({notFound: true}))
    if (err.response && err.response.data.errors) {
      for (const error of err.response.data.errors) {
        // TODO: Implement toast if necessary
        console.log(error.message)
      }
    }
  })
}

export const createRegistry = dispatch => {
  graphQL({query: CREATE_REGISTRY}).then(res => {
    if (res.data.data) {
      let registryId = res.data.data.createRegistry.registry.id
      if (registryId) {
        dispatch(setOnboardingRegistry(registryId))
      }
    }
  }).catch(err => {
    console.log(err.response)
  })
}