const TOGGLE_MUST_HAVE = `
   mutation ToggleMustHaveProduct($productId: Int!, $mustHaveValue: Boolean! $quantity: Int!) {
    updateRegistryItems(registryItemId:$productId, registryItemData:{
    quantity: $quantity,
    mostWanted:$mustHaveValue,
     }){
      message
    }
   }
`

const ADD_REGISTRY_ITEM = `
   mutation addRegistryItems($registryId:Int!,$input: [StoreInput])
   { addRegistryItems(registryId:$registryId,registryItems: $input) {
     message
     }
   }
`

const UPDATE_REGISTRY = `
  mutation updateRegistryDetail($registryId: Int!, $input: RegistryInput!){
    updateRegistry(registryId: $registryId, registryData: $input) {
      registry{
      name,
      image{
            id,image
        }
      }
    }
  }
`;

const UPDATE_VIEW_REGISTRY_POISTION = `
  mutation registryItemsOrder($registryId:Int!, $registryItems: [RegistryItemOrderInput]){
    registryItemsOrder(registryId: $registryId,registryItems: $registryItems){
      message
    }
  }
`;

const UPDATE_REGISTRY_URL = `
mutation updateRegistryUrl($registryId:Int!, $urlSlug:String!){
  checkUpdateRegistryUrlSlug(registryId:$registryId,urlSlugInput:{
    urlSlug:$urlSlug
  }){
    message,
    registry{
      id
    }
  }
}
`

const REGISTER_USER = `
mutation register_user($name: String!, $email: String!, $password: String!){
  registerUser(registerInput:{
      firstName:$name,
      email:$email,
      password:$password
  }
 ){
    user{
      id
    },
    token,
    redirectionUrl
  }
}
`
const SAVE_QUESTION_RESPONSE = `
mutation addRegistryQuestionOption($registryId: Int!, $answers: [RegistryQuestionOptionInput]!, $returnTo: String){
  addRegistryMultipleQuestionOption(registryId: $registryId, registryQuestionOptionInput: $answers, returnTo: $returnTo){
    message,
    redirectionUrl
  }
}
`

const SAVE_REGISTRY_DATA = `
mutation saveRegistryData($registryData:RegistryInput!, $registryItems:[StoreInput]){
  importRegistry(registryData: $registryData, registryItems: $registryItems) {
    message
  }
}
`

const LOGIN_USER = `
mutation login_user($email: String!, $password: String!){
  loginUser(loginInput:{
      email:$email,
      password:$password
  }
 ){
    user{
      id
    },
    token,
    redirectionUrl
  }
}
`

const ADD_PARTNER_ACCOUNT = `
mutation addPartner($partnerDetail: AddPartnerAccountInput!){
  addPartnerAccount(addPartnerAccountInput:$partnerDetail){
    message
  }
}
`

const SAVE_UPDATED_CHECKLIST = `
mutation addDesiredChecklistItemCount($registryId:Int!, $itemCountData:[DesiredItemCountInput]!){
  addDesiredChecklistItemCount(registryId: $registryId, itemCountData: $itemCountData){
    message
  }
}
`

const SAVE_SINGLE_QUESTION_RESPONSE = `
mutation updateOrCreateRegistrySingleQuestionOption($registryId:Int!, $registryQuestionOptionInput: RegistryQuestionOptionInput!){
  updateOrCreateRegistrySingleQuestionOption(registryId: $registryId, registryQuestionOptionInput: $registryQuestionOptionInput){
    message
  }
}
`

const CREATE_REGISTRY = `
mutation{
  createRegistry{
    registry{
      id
    },
    message,
    status
  }
}
`

export {
  TOGGLE_MUST_HAVE,
  ADD_REGISTRY_ITEM,
  UPDATE_REGISTRY,
  UPDATE_VIEW_REGISTRY_POISTION,
  UPDATE_REGISTRY_URL,
  SAVE_REGISTRY_DATA,
  REGISTER_USER,
  SAVE_QUESTION_RESPONSE,
  LOGIN_USER,
  ADD_PARTNER_ACCOUNT,
  SAVE_UPDATED_CHECKLIST,
  SAVE_SINGLE_QUESTION_RESPONSE,
  CREATE_REGISTRY
};
