import React from "react";
import {Redirect, Route, Switch} from "react-router-dom";
import route from "utils/routes";
import {connect} from "react-redux";
import RegistryHero from '../registry-hero/RegistryHero';
import Onboarding from "../onboarding/Onboarding";
import VerifyToken from '../Login/VerifyToken/VerifyToken'
import {updateRegistryDetail, getOnboardingQuestions, createRegistry} from "utils/commonFunction";
import {Cookies} from "react-cookie";
import {verifyAccessToken} from "../../api/axios";
import {setLoggingStatus, updateRegistryInfo} from "../registry/RegistryActions";
import PersonaliseChecklist from "../personalise-checklist/PersonaliseChecklist";
import EditChecklist from "modules/personalise-checklist/components/edit-checklist/EditChecklist";
import GuestView from "../guest-view/GuestView";
import MyRegistry from "../my-registry/MyRegistry";
import ViewRegistry from "../view-registry/ViewRegistry";

class ViewsContainer extends React.Component {

  state = {
    displayContent: false,
    foundRegistry: false
  }

  componentDidMount() {
    const cookies = new Cookies();
    const access_token = cookies.get("access_token")
    if (access_token) {
      verifyAccessToken({token: access_token}).then(res => {
        if (res.data.data.customer) {
          this.props.dispatch(setLoggingStatus(true))
          if (res.data.data.registries.length > 0) {
            this.setState({
              foundRegistry: true
            })
            updateRegistryDetail(this.props.dispatch);
          } else {
            this.props.dispatch(updateRegistryInfo({notFound: true}))

            //  Creating New Registry
            createRegistry(this.props.dispatch)
          }
        }
      }).catch(err => {
        console.error(err)
        this.props.dispatch(setLoggingStatus(false))
        this.props.dispatch(updateRegistryInfo({notFound: true}))
      }).finally(() => {
        this.setState({
          displayContent: true
        })
      })
    } else {
      this.setState({
        displayContent: true
      })
      this.props.dispatch(updateRegistryInfo({notFound: true}))
    }
  }

  render() {

    // if (this.props.stateRouter.location.pathname.search(route.GUEST_VIEW.HOME) !== -1) {
    //   console.log(route.GUEST_VIEW.HOME+"/:registryName")
    //   return <Redirect to={this.props.stateRouter.location.pathname}/>
    // }

    // Redirecting user to onboarding is no registry found
    // if (this.props.stateRouter.location.pathname.search(route.ONBOARDING.HOME) === -1 && !this.props.stateRegistryInfo.id && !this.state.foundRegistry && this.state.displayContent) {
    //   console.log(this.props.stateRouter.location.pathname)
    //   return <Redirect to={route.ONBOARDING.HOME}/>
    // }

    if (this.props.stateOnboardingQuestions.length <= 0) {
      getOnboardingQuestions(this.props.dispatch)
    }

    return (
      <>
        {this.state.displayContent ?
          <Switch>
            <Route exact path={route.PERSONALIZE.CHECKLIST} component={EditChecklist}/>
            <Route exact path={route.PERSONALIZE.HOME} component={PersonaliseChecklist}/>
            <Route exact path={route.ONBOARDING.HOME + "/:questionId?"} component={Onboarding}/>
            <Route exact path={route.MY_REGISTRY.HOME} component={RegistryHero}/>
            <Route exact path={route.VIEW_REGISTRY.HOME} component={RegistryHero}/>
            <Route exact path={route.GUEST_VIEW.HOME + "/:registrySlug"} component={GuestView}/>
            <Route path={route.REGISTRY.HOME} component={RegistryHero}/>
            <Route path={route.LOGIN.VERIFY_TOKEN} component={VerifyToken}/>
            <Route render={() => <div>DEFAULT</div>}/>
          </Switch>
          : null}
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateOnboardingQuestions: state.onboarding.questions,
    stateRouter: state.router
  }
}

export default connect(
  mapStateToProps
)(ViewsContainer);
