import React, { useEffect } from "react";
import ViewRegistryHeader from "../view-registry-header/ViewRegistryHeader";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import { toggleMustHaveProduct, removeProductFromCartHandler, getProducts, updateViewRegistryPosition, getCategories } from "utils/commonFunction";
import { useSelector, useDispatch } from 'react-redux';
import SortFilterSection from '../../components/sort-filter-section/SortFilterSection';
import { setViewRegistryProducts, clearViewFilter } from '../../ViewRegistryActions';
import {
  sortableContainer,
  sortableElement,
} from 'react-sortable-hoc';
import arrayMove from 'array-move';
import './ViewRegistryHome.scss';
import ViewFilter from "modules/view-registry/components/view-filter/ViewFilter";
import Button from "modules/commons/components/button-component/ButtonComponent";

const ViewRegistryHome = () => {

  const dispatch = useDispatch()
  let stateAddedProducts = useSelector(state => state.myRegistry.addedProducts)
  let stateViewRegistryProducts = useSelector(state => state.viewRegistry.viewRegistryProducts)
  const activeSortFilterState = useSelector(state => state.viewRegistry.activeSortFilter)
  const stateFilterApplied = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)
  const stateRegistryInfo = useSelector(state => state.registry.info)
  const stateCategoriesInfo = useSelector(state => state.myRegistry.checkList)
  const stateGuestViewRegistry = useSelector(state => state.viewRegistry.guestViewRegistry)

  // Constant variables for filters
  const AVAILABLE = "available"
  const RESERVED = "reserved"
  const PURCHASED = "purchased"
  const PRICE_1 = "price-1"
  const PRICE_2 = "price-2"
  const PRICE_3 = "price-3"
  const MONICA_AND_ANDY = "monicaandandy"
  const ELSEWHERE = "elsewhere"

  const SortableItem = sortableElement(({ card, index }) => (
    <li className="card-list-item">
      <AddedCard
        data={card} key={"addedCard" + index}
        toggleMustHave={() => toggleMustHaveProduct(card, dispatch)}
        draggable={true}
        disabledDrag={card.disabledDrag}
        removeProductFromCartHandler={() => removeProductFromCartHandler(card, stateRegistryInfo.id, dispatch)}
      />
    </li>
  ));

  let onSortEnd = ({ oldIndex, newIndex }) => {
    let itemsOldPosition = arrayMove([...stateViewRegistryProducts], oldIndex, oldIndex)
    let items = arrayMove([...stateViewRegistryProducts], oldIndex, newIndex)
    dispatch(setViewRegistryProducts(items))
    updateViewRegistryPosition(stateRegistryInfo.id, items, itemsOldPosition, dispatch)
  };

  const SortableContainer = sortableContainer(({ children }) => {
    return <ul className="p-0">{children}</ul>;
  });

  const getSortedProductsByFilters = (addedProducts, filterApplied) => {
    let newSortedProducts = [...addedProducts]
    const childFilterArray = [...filterApplied]

    // For Gift Status
    if ((childFilterArray.includes(AVAILABLE)) ||
      (childFilterArray.includes(PURCHASED)) ||
      (childFilterArray.includes(RESERVED))) {
      newSortedProducts = newSortedProducts.filter(product => {
        // (!product.isReserved || !product.isPurchased) || product.isPurchased || product.isReserved)
        if ((childFilterArray.includes(AVAILABLE) && (!product.isReserved || !product.isPurchased))) {
          return true
        }
        if ((childFilterArray.includes(PURCHASED) && product.isPurchased)) {
          return true
        }
        if ((childFilterArray.includes(RESERVED) && product.isReserved)) {
          return true
        }
      })
    }

    // For Store name
    if ((childFilterArray.includes(MONICA_AND_ANDY) ||
      (childFilterArray.includes(ELSEWHERE)))) {

      newSortedProducts = newSortedProducts.filter(product => {
        if ((childFilterArray.includes(MONICA_AND_ANDY) && product.item.store.storeName === MONICA_AND_ANDY)) {
          return true
        }
        if ((childFilterArray.includes(ELSEWHERE) && product.item.store.storeName !== MONICA_AND_ANDY)) {
          return true
        }
      })
    }

    // For Price
    if ((childFilterArray.includes(PRICE_1) ||
      (childFilterArray.includes(PRICE_2)) ||
      (childFilterArray.includes(PRICE_3)))) {

      newSortedProducts = newSortedProducts.filter(product => {
        if ((childFilterArray.includes(PRICE_1) && product.item.price < 50)) {
          return true
        }
        if ((childFilterArray.includes(PRICE_2) && (product.item.price >= 50 && product.item.price <= 100))) {
          return true
        }
        if ((childFilterArray.includes(PRICE_3) && product.item.price > 100)) {
          return true
        }
      })
    }

    // For Checklist category
    let appliedCategoryProduct = []
    for (let product of newSortedProducts) {
      // TODO: change category
      let parentCat = getParentCategory(product.registryCategories[0].id)
      if (childFilterArray.includes(parentCat ? parentCat.id : '')) {
        appliedCategoryProduct.push(product)
        // appliedCategory.push(getParentCategory(product.item.category.id).id)
      }
    }
    if (appliedCategoryProduct.length) {
      let tempNewSortedProducts = []
      for (let product of appliedCategoryProduct) {
        let temp = newSortedProducts.filter(prod => prod.id === product.id)
        if (temp.length) {
          tempNewSortedProducts.push(...temp)
        }
      }
      if (tempNewSortedProducts.length) {
        newSortedProducts = tempNewSortedProducts
      }
    }

    // Return Filtered Product list
    return newSortedProducts
  }

  const getParentCategory = (id) => {
    return [...stateCategoriesInfo].find((parentCategory) => parentCategory.subCategories.find((subCategory) => subCategory.id === id))
  }

  const getSortedFilteredProducts = (addedProducts, activeSortFilterState, viewRegistryProducts, filterApplied, registryInfo) => {
    let sortedProducts = [];
    let sortedData = null;
    let newSortedProducts = null

    if (filterApplied.length > 0) {
      newSortedProducts = getSortedProductsByFilters(addedProducts, filterApplied)
    }
    else {
      newSortedProducts = [...addedProducts]
    }

    if (activeSortFilterState.sortId === 1) {
      if (filterApplied.length > 0) {
        sortedData = newSortedProducts
      }
      else if (registryInfo.isFeatured) {
        sortedData = viewRegistryProducts ? [...viewRegistryProducts] : [];
      }
      else {
        sortedData = sortByMustHave([...newSortedProducts])
        if (sortedData.length > 0) {
          let viewRegistryProductsId = sortedData.map((productData, index) => {
            return { 'id': productData.id }
          })
        }
      }
    }

    else if (activeSortFilterState.sortId === 2) {
      sortedData = sortByLowToHigh([...newSortedProducts])
    }
    else if (activeSortFilterState.sortId === 3) {
      sortedData = sortByHighToLow([...newSortedProducts])
    }

    let viewRegistryProductsId = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })

    for (let product of viewRegistryProductsId) {
      for (let addProduct of addedProducts) {
        if (product.id === addProduct.id) {
          sortedProducts.push(addProduct);
          break;
        }
      }
    }
    return sortedProducts;
  }

  const sortByMustHave = (productList) => {
    return productList.sort(function (a, b) { return b.mostWanted - a.mostWanted });
  }

  const sortByFeatured = (productList) => {
    return productList.sort(function (a, b) { return a.isFeatured - b.isFeatured });
  }

  const sortByLowToHigh = (productList) => {
    return productList.sort((a, b) => { return a.item.price - b.item.price });
  }

  const sortByHighToLow = (productList) => {
    return productList.sort((a, b) => { return b.item.price - a.item.price });
  }

  //Clear filter state
  const clearFilterHandler = (ev) => {
    ev.preventDefault()
    dispatch(clearViewFilter())
  }

  useEffect(() => {
    if(stateCategoriesInfo.length === 0){
      getCategories(stateRegistryInfo.id, dispatch);
    }
    if (stateAddedProducts.length === 0) {
      getProducts(stateRegistryInfo.id, dispatch);
    }
    // if (stateAddedProducts.length > 0 && stateViewRegistryProducts.length === 0) {
    //   let sortedData = sortByFeatured([...stateAddedProducts])
    //   let viewRegistryProducts = sortedData.map((productData, index) => {
    //     return { 'id': productData.id }
    //   })
    //   dispatch(setViewRegistryProducts(viewRegistryProducts))
    // }
  })

  useEffect(() => {
    let sortedData = sortByFeatured([...stateAddedProducts])
    let viewRegistryProducts = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })
    dispatch(setViewRegistryProducts(viewRegistryProducts))
  }, [stateAddedProducts])

  // updateFilterList(stateAddedProducts)
  let filteredSortProducts = getSortedFilteredProducts(stateAddedProducts, activeSortFilterState, stateViewRegistryProducts, stateFilterApplied, stateRegistryInfo)

  return (
    <>
      <ViewRegistryHeader />
      <div className="container mt-7">
        <div className="row">
          <div className="col-2">
            <ViewFilter />
          </div>
          <div className="col"></div>
          <div className="col-9">

            {/* Top Filter Section */}
            <SortFilterSection productCount={filteredSortProducts.length} />

            {/* Added Products */}
            {filteredSortProducts.length > 0 ?
              <div className="mt-5">
                <SortableContainer onSortEnd={onSortEnd} helperClass="drag-add-card" axis="y" lockAxis="y" useDragHandle>
                  {filteredSortProducts.map((card, index) =>
                    <SortableItem key={`item-${index}`} index={index} card={{ ...card, disabledDrag: activeSortFilterState.sortId !== 1 }} disabled={activeSortFilterState.sortId !== 1 || stateFilterApplied.length !== 0} />
                  )}
                </SortableContainer>
              </div> :
              <div className="d-flex flex-column empty-filter mt-6">
                <div className="font-body empty-filter-title text-center mx-auto mt-10">
                  <p>We couldnt find any gifts that matches your filters...</p>
                  <p>Please try clearing Filters to view gifts.</p>
                </div>
                <div className="mx-auto mt-3">
                  <Button
                    variant="primary"
                    className="px-3 py-1 font-medium empty-filter-title text-body"
                    onClick={(ev) => clearFilterHandler(ev)}
                  >
                    Clear Filters
                  </Button>
                </div>
              </div>}
          </div>
        </div>
      </div>
    </>
  )
}

export default ViewRegistryHome;