import React from "react";
import "./Onboarding.scss";
import {connect} from "react-redux";
import {Redirect} from "react-router";
import routes from "utils/routes";
import Option from "../commons/components/questions/option/Option";
import {addQuestionAnswer, updateQuestionAnswer} from "./OnboardingAction";
import Button from "../commons/components/button-component/ButtonComponent";
import {toast, ToastContainer} from "react-toastify";
import {
  REGISTER_USER,
  SAVE_QUESTION_RESPONSE,
  LOGIN_USER,
  ADD_PARTNER_ACCOUNT,
  CREATE_REGISTRY
} from "../../api/mutations";
import {graphQLNoToken, graphQLNoHeader} from "../../api/axios";
import {Cookies} from "react-cookie";
import {setLoggingStatus, setOnboardingRegistry} from "../registry/RegistryActions";
import {updateRegistryDetailState} from "../../utils/commonFunction";
import {GET_REGISTRIES} from "../../api/queries";

class Onboarding extends React.Component {

  //EMAIL PATTERN
  MAIL_FORMAT = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

  // Static titles
  LOGIN_TITLE = "Sign in for free gifts, free shipping, and exclusive access to our limited edition prints"
  SIGNUP_TITLE = "Lets make it official. Time to create your account."
  PARTNER_TITLE = "Your partners details please"

  // Answer type mapping
  RADIO = "Radio"
  LOGIN = "Login"
  PARTNER = "Partner"

  state = {
    parentId: null,
    question: {},
    login: false,
    toastMessage: "",
    signUpErrors: {},
    signInErrors: {}
  }

  componentDidMount() {
    window.onpopstate = () => {
      if (this.props.location.pathname.search(routes.ONBOARDING.HOME) >= 0) {
        this.setQuestionBasedOnId(this.props.match.params['questionId'])
        this.clearToastMessage()
      }
    }
  }

  clearErrorState = () => {
    this.setState({
      signUpErrors: {},
      signInErrors: {}
    })
  }

  createRegistry = () => {
    let cookies = new Cookies();
    graphQLNoHeader({query: CREATE_REGISTRY}, {
      headers: {"Authorization": cookies.get("access_token") ? "Token " + cookies.get("access_token") : ""}
    }).then(res => {
      if (res.data.data) {
        let registryId = res.data.data.createRegistry.registry.id
        if (registryId) {
          this.props.dispatch(setOnboardingRegistry(registryId))
          this.submitAllAnswers(registryId)
        }
      }
    }).catch(err => {
      console.error(err.response)
    })
  }

  checkValidEmail = (email) => {
    return email.match(this.MAIL_FORMAT)
  }

  submitAllAnswers = (optionalRegistryId = null) => {
    if (this.props.stateOnboardingAnswers.length > 0 && (this.props.stateOnboardingRegistry || optionalRegistryId)) {
      const registryId = optionalRegistryId ? optionalRegistryId : this.props.stateOnboardingRegistry;
      let action = null;
      let returnToURL = null;

      const response = this.props.stateOnboardingAnswers.map(answer => {
        action = answer.response.action ? answer.response.action : null;
        return {
          question: answer.question,
          questionOptionInput: [
            {
              option: answer.response.option,
              optionText: answer.response.optionText
            }
          ]
        }
      })

      if (action) {
        switch (action) {
          case "build-registry":
            returnToURL = window.location.origin + routes.MY_REGISTRY.HOME
            break
          case "registry-quiz":
            returnToURL = window.location.origin + routes.PERSONALIZE.HOME
            break
          default:
            returnToURL = window.location.origin + routes.REGISTRY.HOME
            break
        }
      }

      const cookies = new Cookies();
      let reqData = {
        query: SAVE_QUESTION_RESPONSE,
        operationName: "addRegistryQuestionOption",
        variables: {
          registryId: registryId,
          answers: response
        }
      }

      if (action && returnToURL) {
        reqData.variables['returnTo'] = returnToURL
      }
      graphQLNoHeader(reqData, {
        headers: {"Authorization": cookies.get("access_token") ? "Token " + cookies.get("access_token") : ""}
      }).then(res => {
        if (res.data.data.addRegistryMultipleQuestionOption) {
          if (res.data.data.addRegistryMultipleQuestionOption.redirectionUrl) {
            window.open(res.data.data.addRegistryMultipleQuestionOption.redirectionUrl, "_self")
          }
        }
      }).catch(err => {
        console.error(err);
      })
    }
  }

  clearToastMessage = () => {
    this.setState({
      toastMessage: ""
    })
  }

  setToastMessage = () => {
    if (this.state.question.toastMessage) {
      this.setState({
        toastMessage: this.state.question.toastMessage
      })
    } else {
      this.clearToastMessage()
    }
  }

  gotoNextQuestion = (question, parentId, option = {}) => {
    let nextQuestionId = null;
    let returnNext = false;
    const idToMatch = parentId ? parentId : question.id;
    // Only works for 1 follow up question
    if (option.followUpQuestions && option.followUpQuestions.length > 0) {
      nextQuestionId = option.followUpQuestions[0].question.id;
    } else {
      for (let ques of this.props.stateOnboardingQuestions) {
        if (returnNext) {
          nextQuestionId = ques.id
          break
        }
        if (ques.id === idToMatch) {
          returnNext = true;
        }
      }
    }

    if (nextQuestionId === null && returnNext) {
      //  This is last question
      if (this.props.stateLoggedIn && this.state.redirectionUrl) {
        // window.open(this.state.redirectionUrl, "_self")
      }
    } else if (nextQuestionId) {
      this.setState({
        parentId: null
      })

      this.setQuestionBasedOnId(nextQuestionId)
      this.setToastMessage()
      this.props.history.push(routes.ONBOARDING.HOME + `/${nextQuestionId}`)
    }
  }

  setQuestionBasedOnId = (questionId) => {
    if (questionId) {
      let finalQuestion = {};
      let foundQuestion = false;
      let parentId = null;
      for (const question of this.props.stateOnboardingQuestions) {
        if (foundQuestion) {
          break;
        }
        if (question.id === questionId) {
          finalQuestion = question;
          foundQuestion = true;
          break;
        }
        for (const option of question.options) {
          if (option.followUpQuestions.length > 0) {
            for (const followUpQuestion of option.followUpQuestions) {
              if (followUpQuestion.question.id === questionId) {
                finalQuestion = followUpQuestion.question;
                parentId = followUpQuestion.parentQuestion;
                foundQuestion = true
                break;
              }
            }
          }
        }
      }

      if (finalQuestion.id) {
        this.setState({
          question: finalQuestion,
          parentId: parentId
        })
      } else {
        console.log("No Question Found");
      }
    }
  }

  addAnswerToState = (response) => {
    const alreadyAnswered = this.props.stateOnboardingAnswers.find(el => el.question === this.state.question.id)
    if (alreadyAnswered) {
      // This will delete the answers that were given after the matched response
      let newState = []
      for (let answer of this.props.stateOnboardingAnswers) {
        if (answer.question === this.state.question.id) {
          answer.response = response;
          newState.push(answer)
          break;
        }
        newState.push(answer)
      }
      this.props.dispatch(updateQuestionAnswer(newState))
    } else {
      this.props.dispatch(addQuestionAnswer({question: this.state.question.id, response: response}))
    }

    // this.submitAllAnswers()
  }

  answerRadioQuestion = (option) => {
    this.addAnswerToState({option: option.id, optionText: option.value, action: option.action});
    this.gotoNextQuestion(this.state.question, this.state.parentId, option);
  }

  watchNextBtn = (ev) => {
    this.setState({
      nextBtnState: ev.target.value.length <= 0
    })
  }

  answerDataQuestion = (response) => {
    this.addAnswerToState({option: 0, optionText: response})
    this.gotoNextQuestion(this.state.question, this.state.parentId);
  }

  textInputTypeChangeHandler = (ev, inputRefs) => {
    let enableBtn = false;

    for (const inputRef of inputRefs) {
      if (inputRef.current.value.length <= 0) {
        enableBtn = true
        break
      }
    }

    this.setState({
      nextBtnState: enableBtn
    })
  }

  changeLoginState = (ev, value, refs) => {
    ev.preventDefault()
    for (let ref of refs){
      if (ref.current){
        ref.current.value = ""
      }
    }
    this.setState({
      login: value
    })
  }

  setErrorBasedOnResponse = (err, type) => {
    if (err.response.data && err.response.data.errors) {
      for (let error of err.response.data.errors) {
        let parentErrors = err.response.data.errors

        if (parentErrors.length > 0) {
          for (const subErrors of parentErrors) {
            if (subErrors.errors && (subErrors.errors instanceof Array ? subErrors.errors.length > 0 : true)) {
              let newErrorState = {}
              for (const error in subErrors.errors) {
                newErrorState[error] = subErrors.errors[error].length > 0 ? subErrors.errors[error][0] : ""
              }
              switch (type) {
                case "signUp":
                  this.setState({
                    signUpErrors: newErrorState
                  })
                  break
                case "signIn":
                  this.setState({
                    signInErrors: newErrorState
                  })
                  break
              }
            } else {
              toast.error(error.message)
            }
          }
        }
      }
    }
    console.error(err.response)
  }

  signInClickHandler = (emailRef, passwordRef) => {
    if (emailRef.current.value.length <= 0 || passwordRef.current.value.length <= 0) {
      toast.error("Please enter all fields")
      return;
    }

    // Checking for valid email
    if (!this.checkValidEmail(emailRef.current.value)) {
      toast.error("Invalid email address");
      return;
    }

    let reqData = {
      query: LOGIN_USER,
      operationName: "login_user",
      variables: {
        email: emailRef.current.value,
        password: passwordRef.current.value
      }
    }

    graphQLNoToken(reqData).then(res => {
      if (res.data.data.loginUser) {
        const cookies = new Cookies();
        cookies.set("access_token", res.data.data.loginUser.token, {path: "/"})

        // Checking if user has registry
        let data = {
          query: GET_REGISTRIES
        }
        graphQLNoHeader(data, {headers: {"Authorization": "Token " + res.data.data.loginUser.token}}).then(registryRes => {
          if (registryRes.data.data.registries.edges.length > 0) {
            window.open(routes.MY_REGISTRY.HOME, "_self")
            // updateRegistryDetailState(this.props.dispatch, registryRes.data.data.registries.edges);
          } else {

            // Creating Registry
            this.createRegistry()
          }

        }).catch(err => {
          console.error(err);
        })
        this.setState({
          redirectionUrl: res.data.data.loginUser.redirectionUrl
        })
        this.props.dispatch(setLoggingStatus(true))
        this.submitAllAnswers()
      }
    }).catch(err => {
      if (err.response.status === 401) {
        toast.error("Invalid email or password");
        return;
      }
      this.setErrorBasedOnResponse(err, "signIn")
    })
  }

  getSignInContent = () => {
    const emailRef = React.createRef()
    const passwordRef = React.createRef()
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className={["onboarding-input py-1 px-2 text-body mb-0 mt-1", this.state.signInErrors.email ? "onboarding-input-error" : ""].join(" ")}
            type="email"
            name="signIn-email"
            ref={emailRef}
            placeholder="johndoe@email.com"
          />
          {this.state.signInErrors.email ?
            <p className="text-sm onboarding-error-message mt-1">{this.state.signInErrors.email}</p>
            : null}
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Password</span>
          <input
            className={["onboarding-input py-1 px-2 text-body mb-0 mt-1", this.state.signInErrors.password ? "onboarding-input-error" : ""].join(" ")}
            type="password"
            name="signIn-password"
            ref={passwordRef}
            placeholder="Password"
          />
          {this.state.signInErrors.password ?
            <p className="text-sm onboarding-error-message mt-1">{this.state.signInErrors.password}</p>
            : null}
        </div>
        <Button
          variant="primary"
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.signInClickHandler(emailRef, passwordRef)}
        >Sign In</Button>
        {/*TODO : Add Api on Onclick of the button above*/}
        <h4 className="mt-11">Don't have an account? <a href="#" className="font-medium onboarding-link-text"
                                                        onClick={(ev) => this.changeLoginState(ev, false, [emailRef, passwordRef])}
        >Sign Up</a></h4>
      </>
    )
  }

  signUpClickHandler = (nameRef, emailRef, passwordRef) => {
    if (nameRef.current.value.length <= 0 || emailRef.current.value.length <= 0 || passwordRef.current.value.length <= 0) {
      toast.error("Please enter all fields")
      return;
    }

    // Checking for valid email
    if (!this.checkValidEmail(emailRef.current.value)) {
      this.setState({
        signUpErrors: {
          email: "Invalid email address"
        }
      })
      return;
    }

    // Clearing all error messages
    this.clearErrorState()

    let reqData = {
      query: REGISTER_USER,
      operationName: "register_user",
      variables: {
        name: nameRef.current.value,
        email: emailRef.current.value,
        password: passwordRef.current.value
      }
    }

    graphQLNoToken(reqData).then(res => {
      if (res.data.data.registerUser) {
        const cookies = new Cookies();
        cookies.set("access_token", res.data.data.registerUser.token, {path: "/"})
        this.setState({
          redirectionUrl: res.data.data.registerUser.redirectionUrl
        })
        this.createRegistry()
        this.submitAllAnswers()
        this.props.dispatch(setLoggingStatus(true))
      }
    }).catch(err => {
      this.setErrorBasedOnResponse(err, "signUp")
    })
  }

  getSignUpContent = () => {
    let nameRef = React.createRef()
    let emailRef = React.createRef()
    let passwordRef = React.createRef()
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Name</span>
          <input
            className={["onboarding-input py-1 px-2 text-body mb-0 mt-1", this.state.signUpErrors.name ? "onboarding-input-error" : ""].join(" ")}
            type="text"
            name="name"
            placeholder="John Doe"
            ref={nameRef}
          />
          {this.state.signUpErrors.name ?
            <p className="text-sm onboarding-error-message mt-1">{this.state.signUpErrors.name}</p>
            : null}
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className={["onboarding-input py-1 px-2 text-body mb-0 mt-1", this.state.signUpErrors.email ? "onboarding-input-error" : ""].join(" ")}
            type="email"
            name="email"
            placeholder="johndoe@email.com"
            ref={emailRef}
          />
          {this.state.signUpErrors.email ?
            <p className="text-sm onboarding-error-message mt-1">{this.state.signUpErrors.email}</p>
            : null}
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Password</span>
          <input
            className={["onboarding-input py-1 px-2 text-body mb-0 mt-1", this.state.signUpErrors.password ? "onboarding-input-error" : ""].join(" ")}
            type="password"
            name="password"
            placeholder="Password"
            ref={passwordRef}
          />
          {this.state.signUpErrors.password ?
            <p className="text-sm onboarding-error-message mt-1">{this.state.signUpErrors.password}</p>
            : null}
        </div>
        <Button
          variant="primary"
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.signUpClickHandler(nameRef, emailRef, passwordRef)}
        >Sign Up</Button>
        <h4 className="mt-11">Already have an account? <a href="#" className="font-medium onboarding-link-text" tabindex
                                                          onClick={(ev) => this.changeLoginState(ev, true, [nameRef, emailRef, passwordRef])}>Sign
          In</a></h4>
      </>
    )
  }

  savePartnerDetail = (nameRef, emailRef) => {
    if (nameRef.current.value.length <= 0 || emailRef.current.value.length <= 0) {
      toast.error("Please enter all fields")
      return
    }

    // Checking for valid email
    if (!this.checkValidEmail(emailRef.current.value)) {
      toast.error("Invalid email address");
      return;
    }

    const cookies = new Cookies();
    let reqData = {
      query: ADD_PARTNER_ACCOUNT,
      operationName: "addPartner",
      variables: {
        partnerDetail: {
          email: emailRef.current.value,
          firstName: nameRef.current.value
        }
      }
    }
    graphQLNoHeader(reqData, {
      headers: {"Authorization": cookies.get("access_token") ? "Token " + cookies.get("access_token") : ""}
    }).then(res => {
      if (res.data.data.addPartnerAccount) {
        this.gotoNextQuestion(this.state.question, this.state.parentId);
      } else {
        toast.error("Some error occurred");
      }
    }).catch(err => {
      toast.error("Some error occurred");
      console.error(err.response.data);
    })
  }

  getPartnerContent = () => {
    const nameRef = React.createRef()
    const emailRef = React.createRef()
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Name</span>
          <input
            className="onboarding-input py-1 px-2 text-body mb-0 mt-1"
            type="text"
            name="name"
            placeholder="Name"
            ref={nameRef}
            onChange={(ev) => this.textInputTypeChangeHandler(ev, [nameRef, emailRef])}
          />
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1 mb-0"
            type="email"
            name="email"
            placeholder="Your Email"
            ref={emailRef}
            onChange={(ev) => this.textInputTypeChangeHandler(ev, [nameRef, emailRef])}
          />
        </div>
        <Button
          variant="primary"
          disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.savePartnerDetail(nameRef, emailRef)}
        >Next</Button>
      </>
    )
  }

  render() {
    if (this.props.stateOnboardingAnswers) {
      this.submitAllAnswers()
    }

    // Redirecting user to registry home if registry found
    if (this.props.stateRegistryInfo.id) {
      return <Redirect to={routes.MY_REGISTRY.HOME}/>
    }

    // Redirecting to first question if not answered any
    if (this.props.stateOnboardingQuestions.length > 0
      && this.props.stateOnboardingAnswers.length <= 0
      && this.props.location.pathname !== routes.ONBOARDING.HOME + `/${this.props.stateOnboardingQuestions[0].id}`) {
      return <Redirect to={routes.ONBOARDING.HOME + `/${this.props.stateOnboardingQuestions[0].id}`}/>
    }

    if (!this.state.question.title && this.props.stateOnboardingQuestions.length > 0 && this.props.stateRouter.location.pathname !== routes.ONBOARDING.HOME + `/${this.state.question.id}`) {
      this.setQuestionBasedOnId(this.props.match.params['questionId'])
    }

    // Skipping Login Question if user is already logged in
    if (this.props.stateLoggedIn && this.state.question.answerInputType === this.LOGIN) {
      // If user already logged into M&A Store
      this.setState((prevState, _) => ({
        redirectionUrl: prevState.redirectionUrl ? prevState.redirectionUrl : routes.MY_REGISTRY.HOME
      }))
      this.gotoNextQuestion(this.state.question, null);
    }

    if (this.state.toastMessage) {
      toast.dismiss()
      toast.info(<h4 className="font-medium text-center">{this.state.toastMessage}</h4>)
      setTimeout(() => this.setState({toastMessage: ""}), 4000)
    } else {
      toast.dismiss()
    }

    // Setting all the options for the questions
    let options = []
    let staticQuestionTitle = "";

    if (this.state.question.answerInputType === this.RADIO) {
      options = this.state.question.options.map(option => (
        <Option className="mb-2 mr-4" onClick={() => this.answerRadioQuestion(option)}>
          <h4 className="d-inline">{option.value}</h4>{option.helpText ?
          <span className="text-sm ml-1 onboarding-option-help-text">{option.helpText}</span> : null}</Option>
      ))
    } else if (this.state.question.answerInputType === "Date") {
      let inputRef = React.createRef();
      options.push(
        <div className="d-flex flex-column">
          {/*TODO: Get placeholder from backend*/}
          <input
            type="date"
            className="onboarding-input py-1 px-2 text-body mb-0"
            placeholder="Date"
            ref={inputRef}
            onChange={(ev) => this.watchNextBtn(ev)}
          />
          <Button
            variant="primary"
            disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
            onClick={() => this.answerDataQuestion(inputRef.current.value)}
            className="px-3 py-1 font-medium mt-4 onboarding-next-button"
          >Next</Button>
        </div>
      )
    } else if (this.state.question.answerInputType === "Text") {
      let inputRefs = []
      const inputRef = React.createRef()
      inputRefs.push(inputRef)
      options.push(
        // TODO: Update this part if there is a Text type question
        <div className="d-flex flex-column">
          <input
            className="onboarding-input py-1 px-2 text-body mb-0 mt-1"
            type="Text"
            name="Answer"
            ref={inputRef}
            onChange={(ev) => this.textInputTypeChangeHandler(ev, inputRefs)}
          />
          <Button
            variant="primary"
            disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
            onClick={() => this.answerDataQuestion(inputRef.current ? inputRef.current.value : "")}
            className="mt-4 px-3 py-1 font-medium onboarding-next-button"
          >Next</Button>
        </div>
      )
    } else if (this.state.question.answerInputType === this.LOGIN) {
      let content = null;
      if (this.state.login) {
        staticQuestionTitle = this.LOGIN_TITLE
        content = this.getSignInContent()
      } else {
        staticQuestionTitle = this.SIGNUP_TITLE
        content = this.getSignUpContent()
      }

      options.push(content)
    } else if (this.state.question.answerInputType === this.PARTNER) {
      staticQuestionTitle = this.PARTNER_TITLE;
      options.push(this.getPartnerContent())
    }

    return (
      <>
        <div className="baby-registry-onboarding container">
          <div className="row justify-content-center mt-24">
            <div className="col-12 col-md-6">
            <span
              className="font-body onboading-question">{[this.LOGIN, this.PARTNER].indexOf(this.state.question.answerInputType) !== -1 ? staticQuestionTitle : this.state.question.title}</span>
            </div>
          </div>
          <div className="row justify-content-center mt-6">
            <div className="col-12 col-md-6">
              {options.map(option => option)}
            </div>
          </div>
        </div>
        <ToastContainer
          position="top-center"
          className="onboarding-toast-container"
          toastClassName="onboarding-toast px-3 py-1"
          autoClose={4000}
          hideProgressBar={true}
          newestOnTop={true}
          draggable={false}
          closeOnClick={false}
          closeButton={null}
        />
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateLoggedIn: state.registry.loggedIn,
    stateOnboardingQuestions: state.onboarding.questions,
    stateOnboardingAnswers: state.onboarding.answers,
    stateOnboardingRegistry: state.registry.onboardingRegistry,
    stateRouter: state.router
  }
}

export default connect(
  mapStateToProps
)(Onboarding);