import {VIEW_REGISTRY_ACTION_TYPES} from "./ViewRegistryActions";
import {combineReducers} from "redux";
import defaultData from "data/ReducerDefaultData.json";

const setParentFilter = (state, payload) => {
  let newState = {...state}
  newState.parentFilter = payload
  newState.childFilter = [...state.childFilter]

  return newState
}


const setChildFilter = (state, payload) => {
  let newState = {...state}
  newState.childFilter = [...payload]

  return newState
}

const viewRegistryProducts = (state = [], action) => {
    switch (action.type) {
      case VIEW_REGISTRY_ACTION_TYPES.VIEW_REGISTRY_PRODUCTS:
          return [...action.payload];
      default:
          return state;
    }
}

const activeSortFilter = (state = {sortId: 1, sortTitle: 'Featured'}, action) => {
    switch (action.type) {
      case VIEW_REGISTRY_ACTION_TYPES.ACTIVE_SORT_FILTER:
        return {...action.payload};
      default:
        return state;
    }
  }

const guestViewRegistry = (state = false, action) => {
  switch (action.type) {
    case VIEW_REGISTRY_ACTION_TYPES.IS_GUEST_VIEW:
      return action.payload;
    default:
      return state;
  }
}

const filterCategory = (state = [], action) => {
  switch (action.type) {
    case VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_FILTER_CATEGORY:
      return [...action.payload]
    default:
      return state
  }
}

const viewFilterApplied = (state = defaultData.myRegistry.defaultFilter, action) => {
  switch (action.type) {
    case VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_PARENT_FILTER:
      return setParentFilter(state, action.payload)
    case VIEW_REGISTRY_ACTION_TYPES.SET_VIEW_CHILD_FILTER:
      return setChildFilter(state, action.payload)
    case VIEW_REGISTRY_ACTION_TYPES.CLEAR_VIEW_FILTER:
      return defaultData.myRegistry.defaultFilter
    default:
      return state;
  }
}

export default combineReducers({
    viewRegistryProducts,
    activeSortFilter,
    guestViewRegistry,
    filterCategory,
    viewFilterApplied
})
